<template>
    <div class="regist">
    <h1 class="underline">  </h1>
      <b-form>

      <b-form-group
        style="text-align: left"
        id="input-group-1"
        label="Title."
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="title"
          required
        ></b-form-input>
      </b-form-group>

      <b-form-group
        style="text-align: left"
        id="input-group-1"
        label="writer."
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="writer"
          required
          readonly
        ></b-form-input>
      </b-form-group>

      <b-form-group
        style="text-align: left"
        id="input-group-1"
        label="Content."
        label-for="input-1"
      >
        <b-form-input
          id="input-1"
          v-model="content"
          required
        ></b-form-input>
      </b-form-group>
        <b-button type="" variant="primary" @click="checkValue" ></b-button>
        <b-button type="" variant="success" @click="moveList"></b-button>
      </b-form>
    </div>
</template>

<script>
import http from "@/util/http-common.js";

export default {
  data() {
    return {
      writer: sessionStorage.getItem("userId"),
      title: "",
      content: "",
    };
  },
  methods: {
    //   -   registBook 
    checkValue() {
      //   
      // isbn, , , ,        
      let err = true;
      let msg = "";
      !this.writer &&
        ((msg = " "),
        (err = false),
        this.$refs.writer.focus());
      err &&
        !this.title &&
        ((msg = " "), (err = false), this.$refs.title.focus());
      err &&
        !this.content &&
        ((msg = " "),
        (err = false),
        this.$refs.content.focus());

      if (!err) alert(msg);
      // ,     registBook 
      else this.registArticle();
    },

    registArticle() {
      http
        .post("vue/api/vueboard/free", {
          writer: this.writer,
          title: this.title,
          content: this.content,
        })
        .then(({ data }) => {
          //      .
          let msg = "   .";
          if (data === "success") {
            msg = " .";
          }
          alert(msg);
        });
                  this.moveList();
    },
    moveList() {
      this.$router.push({ name: "QnAList" });
    },
  },
};
</script>

<style></style>
