<template>
<div class="container">
    <div class="input-form-backgroud row">
        <div class="input-form col-md-12 mx-auto">
            <h4 class="mb-3"></h4>
            <form class="validation-form" novalidate>
                <div class="row"> 
                    <div class="col-md-6 mb-3">
                    <label for="userid"></label> <input type="text" class="form-control" id="userid" placeholder="" value="" required  v-model="userid" ref="userid" readonly>
                    </div>
                    <div class="col-md-6 mb-3">
                        <label for="userpassword"></label> <input type="text" class="form-control" id="userpassword" placeholder="" value="" required  v-model="userpassword" ref="userpassword">
                    </div>
                </div>
                    <div class="mb-3">
                        <label for="username"></label> <input type="text" class="form-control" id="username" placeholder="" value="" required  v-model="username" ref="username">
                    </div>
                    <div class="mb-3">
                        <label for="useremail"></label> <input type="email" class="form-control" id="useremail" placeholder="you@example.com" required v-model="useremail" ref="useremail">
                    </div>
                    <div class="row"> 
                    <div class="col-md-6 mb-3">
                        <label for="usersex"></label>
                        <div class = "row">
                            <div class="col-md-6 mb-3">
                            <input type="radio" id="usersexw" value="W" v-model="usersex">
                            <label for="one">W</label>
                            </div>
                            <div class="col-md-6 mb-3">
                            <input type="radio" id="usersexm" value="M" v-model="usersex">
                            <label for="two">M</label>
                            </div>
                        </div>
                    </div>
                    <div class="col-md-6 mb-3">
                        <label for="userage"></label><input type="text" class="form-control" id="userage" placeholder="" value="" required v-model="userage" ref="userage">
                    </div>
                </div>
                    <div class="mb-4"></div>
                    <div class="row" style="text-align: center"> 
                    <div style="width: 48%; margin: 0 auto"><button class="btn btn-primary btn-lg btn-block" @click="checkValue" type="button"></button></div>
                    <div style="width: 48%; margin: 0 auto"><button class="btn btn-primary btn-lg btn-block" @click="DeleteUser" type="button"></button></div>
                    </div>
            </form>
        </div>
    </div>
</div>
</template>

<script>
import http from "@/util/http-common.js";
export default {
        data(){
            return{
                userid: sessionStorage.getItem("userId"),
                userpassword: '',
                username: '',
                useremail: '',
                userage: '',
                usersex: '',
            };
    },
    created() {
    http.get(`vue/api/vueuser/${this.userid}`).then(({ data }) => {
      this.userpassword = data.userPassword;
      this.username = data.userName;
      this.useremail = data.userEmail;
      this.userage = data.userAge;
      this.usersex = data.userSex;
    });
  },
    methods: {
        checkValue() {
      //   
      // isbn, , , ,        
    let err = true;
    let msg = "";
    !this.userid &&
        ((msg = " " ),
        (err = false),
        this.$refs.userid.focus());
    err &&
        !this.userpassword &&
        ((msg = " "),
        (err = false),
        this.$refs.userpassword.focus());
    err &&
        !this.username &&
        ((msg = " "), 
        (err = false), this.$refs.username.focus());
    err &&
        !this.useremail &&
        ((msg = " "),
        (err = false),
        this.$refs.useremail.focus());
    err &&
        !this.userage &&
        ((msg = " "), (err = false), this.$refs.userage.focus());
    err &&
        !this.usersex &&
        ((msg = " "), (err = false));
    if (!err) alert(msg);
      // ,     registBook 
    else this.ModifyUser();
    },
    ModifyUser(){
    console.log("user id : " + this.userid + " user pw : " + this.userpassword + "\n user name : " + this.username + " useremail : " + this.useremail + "\n user age: " + this.userage + " usersex : " + this.usersex);
    http
        .put("vue/api/vueuser/${this.userid}", {
            userId: this.userid,
            userPassword: this.userpassword,
            userName: this.username,
            userEmail: this.useremail,
            userAge : this.userage,
            userSex : this.usersex,
        })
        .then(({ data }) => {
          //      .
        let msg = "   .";
        if (data === "success") {
            msg = " .";
        }
        alert(msg);
        this.$router.push("/");
            });
        },
        DeleteUser(){
            http
                .delete(`vue/api/vueuser/${this.userid}`).then(({ data }) => {
                    let msg = "   .";
            if (data === "success") {
            msg = " .";
            sessionStorage.removeItem("userId");
            this.$router.push({name: "Home"});
            this.$router.go("/");
            }
            alert(msg);
      //  route /list .
        
                });
        }
    },
};
</script>

