<script>
import http from "@/util/http-common.js";

export default {
  created() {
    http.delete(`vue/api/vueboard/notice/${this.$route.params.no}`).then(({ data }) => {
      let msg = "   .";
      if (data === "success") {
        msg = " .";
      }
      alert(msg);
      //  route /list .
      this.$router.push("/noticelist");
    });
  },
};
</script>

